  position: relative;
}

.header_outer {
  width: 100%;
  z-index: 9999;
}
.header_outer .header {
  width: 1660px;
  height: 100px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}
.header_outer .header .logo {
  width: 120px;
  padding: 30px 0;
  box-sizing: border-box;
}
.header_outer .header .logo img {
  width: 100%;
  height: auto;
  display: block;
}
.header_outer .header .gnb {
