}
.header_outer .lnb_menu .lnb_header .logo {
  width: 120px;
  padding: 30px 0;
  box-sizing: border-box;
}
.header_outer .lnb_menu .lnb_header .logo img {
  width: 100%;
  height: auto;
  display: block;
}
.header_outer .lnb_menu .lnb_header .gnb {
