}
.header_outer .lnb_menu .lnb_header .gnb ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header_outer .lnb_menu .lnb_header .gnb ul li {
  font-size: 18px;
  font-weight: bold;
}
.header_outer .lnb_menu .lnb_header .gnb ul .bar {
  background-color: #ccc;
  height: 12px;
  width: 1px;
  margin: 0 10px;
}
.header_outer .lnb_menu .lnb_header .gnb ul a {
  color: black;
  padding: 15px;
}
.header_outer .lnb_menu .lnb_header .icons {
