.footer_outer {
  width: 100%;
  margin: 100px auto 0;
  background-color: #eee;
  position: absolute;
  bottom: 0;
}
.footer_outer .footer {
  width: 1660px;
  margin: 0 auto;
  padding: 60px 0 20px;
  /* content01------------------------------------------------ */
  /* content02------------------------------------------------ */
  /* line------------------------------------------------ */
}
.footer_outer .footer .footer_content_01 {
  display: flex;
  justify-content: space-between;
}
.footer_outer .footer .footer_content_01 .footer_menu {
  width: 70%;
