  font-size: 22px;
}
.footer_outer .footer .footer_content_01 .footer_sns .search_box {
  margin-top: 20px;
  height: 45px;
  position: relative;
}
.footer_outer .footer .footer_content_01 .footer_sns .search_box .search {
  float: left;
  background-color: transparent;
  width: 100%;
  padding-left: 10px;
  height: 45px;
  border: 2px solid #000;
  box-sizing: border-box;
}
.footer_outer .footer .footer_content_01 .footer_sns .search_box .search_btn {
  float: left;
