}
.footer_outer .footer .footer_content_01 .footer_sns .cs_box {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
}
.footer_outer .footer .footer_content_01 .footer_sns .cs_box .nation {
  font-size: 12px;
  line-height: 2;
}
.footer_outer .footer .footer_content_01 .footer_sns .cs_box select {
  border: none;
  border-bottom: 1px solid #000;
  background-color: transparent;
}
.footer_outer .footer .footer_content_01 .footer_sns .cs_box select option {
  background-color: #eee;
  font-size: 12px;
}
.footer_outer .footer .footer_content_02 {
