}
.footer_outer .footer .footer_content_02 .company_info .list {
  margin-top: 58px;
  display: flex;
}
.footer_outer .footer .footer_content_02 .company_info .list li {
  width: 10%;
  color: black;
  font-weight: bold;
  border-right: 1px solid #ccc;
  text-align: center;
}
.footer_outer .footer .footer_content_02 .company_info .list li:first-child {
  text-align: left;
