}
.footer_outer .footer .footer_content_02 .company_info .list li:last-child {
  border-right: none;
}
.footer_outer .footer .footer_content_02 .cs_info {
  float: left;
}
.footer_outer .footer .footer_content_02 .cs_info .cs_btn {
  background-color: transparent;
  width: 100%;
  border-radius: 20px;
  height: 45px;
  border: 1px solid #555;
  font-size: 15px;
}
.footer_outer .footer .footer_content_02 .cs_info .cs_tel {
  font-weight: bold;
  font-size: 30px;
  margin-top: 20px;
}
.footer_outer .footer .footer_content_02 .cs_info .cs_mail {
  font-weight: bold;
  font-size: 25px;
}
.footer_outer .footer .footer_content_02 .cs_info .cs_time {
  font-size: 15px;
  line-height: 2;
}
.footer_outer .footer .footer_content_02 .cs_info .cs_copy {
  margin-top: 8px;
  color: #777;
  font-size: 13px;
  line-height: 2;
}
.footer_outer .footer .line {
  border-bottom: 1px solid #ccc;
  margin: 40px 0;
}

/* ------------------ goods ------------------ */
.goods_box_outer {
  width: 1420px;
  margin: 50px auto 0px;
}
.goods_box_outer .goods_box {
  display: flex;
  justify-content: space-between;
}
.goods_box_outer .goods_box .goods_img {
  display: flex;
  flex-wrap: wrap;
  width: 1100px;
  margin-right: 20px;
  /*       img{
          width: 49%;
          height: auto;
          display: block;
        } */
}
.goods_box_outer .goods_box .goods_img video {
  width: 49%;
  height: auto;
  display: block;
  margin-right: 2%;
}
.goods_box_outer .goods_box .goods_img .swiper {
  width: 49%;
  height: auto;
  display: block;
}
.goods_box_outer .goods_box .goods_img .banner {
  width: 100%;
  height: 200px;
  display: block;
  margin-top: 20px;
}
.goods_box_outer .goods_info {
  width: 300px;
}
.goods_box_outer .goods_info .infobox {
  margin-bottom: 30px;
}
.goods_box_outer .goods_info .title_box {
  margin-bottom: 20px;
}
.goods_box_outer .goods_info .title_box .tag {
  display: flex;
  margin-bottom: 5px;
}
.goods_box_outer .goods_info .title_box .tag li {
  font-size: 14px;
  padding-right: 10px;
}
.goods_box_outer .goods_info .title_box .tag li::before {
  content: "";
  display: inline-block;
  width: 1px;
  height: 12px;
  background-color: #ccc;
  margin-right: 10px;
}
.goods_box_outer .goods_info .title_box .tag li:first-child::before {
  width: 0;
  margin-right: 0;
}
.goods_box_outer .goods_info .content_box {
  font-size: 13px;
  text-align: justify;
  color: #333;
  position: relative;
}
.goods_box_outer .goods_info .content_box .content_btn {
  background-color: #fff;
  border: none;
  font-weight: 400;
  font-size: 11px;
  color: rgb(153, 153, 153);
  text-decoration: underline;
  position: absolute;
  left: 0;
}
.goods_box_outer .goods_info .color_box h4 {
  margin-bottom: 10px;
}
.goods_box_outer .goods_info .color_box ul {
  display: flex;
}
.goods_box_outer .goods_info .color_box ul li {
  margin-right: 10px;
  width: 70px;
  cursor: pointer;
}
.goods_box_outer .goods_info .color_box ul li img {
  width: 100%;
  height: auto;
  display: block;
}
.goods_box_outer .goods_info .size_box .size_title {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}
.goods_box_outer .goods_info .size_box .size_title .stock {
  font-size: 13px;
  cursor: pointer;
  border: 1px solid #000;
  transition: all 0.3s ease-in;
  padding: 0 12px;
}
.goods_box_outer .goods_info .size_box .size_title .stock:hover {
  background-color: #000;
  color: white;
}
.goods_box_outer .goods_info .size_box .size_type {
  display: flex;
  justify-content: space-between;
}
.goods_box_outer .goods_info .size_box .size_type li input {
  display: none;
}
.goods_box_outer .goods_info .size_box .size_type li input:checked ~ label {
  border: 1px solid #000;
}
.goods_box_outer .goods_info .size_box .size_type li label {
  flex: 1;
  padding: 0 5px;
  display: block;
  border: 1px solid rgb(238, 238, 238);
  font-size: 14px;
  width: 100%;
  height: 30px;
  line-height: 30px;
  border-radius: 5px;
  box-sizing: border-box;
  cursor: pointer;
  font-weight: 400;
  font-size: 11px;
}
.goods_box_outer .goods_info .quantity_box h4 {
  margin-bottom: 10px;
}
.goods_box_outer .goods_info .quantity_box ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid rgb(238, 238, 238);
  box-sizing: border-box;
  height: 40px;
  padding: 0 15px;
}
.goods_box_outer .goods_info .quantity_box ul li {
  cursor: pointer;
}
.goods_box_outer .goods_info .price_box ul {
  display: flex;
  justify-content: space-between;
}
.goods_box_outer .goods_info .buy_box ul {
  display: flex;
}
.goods_box_outer .goods_info .buy_box ul li {
  border: 1px solid rgb(238, 238, 238);
  border-radius: 50px;
  width: 50px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  width: 25%;
  margin-right: 2%;
  cursor: pointer;
}
.goods_box_outer .goods_info .buy_box ul .buy {
  width: 50%;
  margin-right: 0%;
  font-weight: bold;
  transition: all 0.3s;
}
.goods_box_outer .goods_info .buy_box ul .buy:hover {
  background-color: #002053;
  border: 1px solid #002053;
  color: #fff;
}

